.btn,
.navbar .navbar-nav > a.btn {
  border-width: $border-thick;
  font-weight: $font-weight-semi;
  font-size: $font-size-large;
  line-height: $line-height;
  border: none;
  position: relative;
  overflow: hidden;
  margin: 4px 1px;
  border-radius: $border-radius-large;
  padding: $padding-btn-vertical $padding-btn-horizontal;
  cursor: pointer;
  animation: rotation 0.3s infinite;

  @include btn-styles($default-color, $default-states-color);

  &:hover,
  &:focus {
    @include opacity(1);
    outline: 0 !important;
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    @include box-shadow(none);
    outline: 0 !important;
  }

  .badge {
    margin: 0;
  }

  &.btn-icon {
    // see above for color variations
    height: $btn-icon-size-regular;
    min-width: $btn-icon-size-regular;
    width: $btn-icon-size-regular;
    padding: 0;
    font-size: $btn-icon-font-size-regular;
    overflow: hidden;
    position: relative;
    line-height: normal;

    &.btn-simple {
      padding: 0;
    }

    &.btn-sm {
      height: $btn-icon-size-small;
      min-width: $btn-icon-size-small;
      width: $btn-icon-size-small;

      .fa,
      .far,
      .fas,
      .tim-icons {
        font-size: $btn-icon-font-size-small;
      }
    }

    &.btn-lg {
      height: $btn-icon-size-lg;
      min-width: $btn-icon-size-lg;
      width: $btn-icon-size-lg;

      .fa,
      .far,
      .fas,
      .tim-icons {
        font-size: $btn-icon-font-size-lg;
      }
    }

    &:not(.btn-footer) .tim-icons,
    &:not(.btn-footer) .fa,
    &:not(.btn-footer) .far,
    &:not(.btn-footer) .fas {
      position: absolute;
      font-size: 1em;
      top: 50%;
      left: 50%;
      transform: translate(-12px, -12px);
      line-height: 1.5626rem;
      width: 24px;
    }

  }

  &:not(.btn-icon) .tim-icons {
    position: relative;
    top: 1px;
  }

  span {
    position: relative;
    display: block;
  }

  &.btn-link.dropdown-toggle {
    color: #9a9a9a;
  }
}

// Apply the mixin to the buttons
// .btn-default { @include btn-styles($default-color, $default-states-color); }
.btn-primary {
  @include btn-styles($primary-color, $primary-states-color);
  color: $text-color;
}

.btn-success {
  @include btn-styles($success-color, $success-states-color);

}

.btn-info {
  @include btn-styles($info-color, $info-states-color);

}

.btn-warning {
  @include btn-styles($warning-color, $warning-states-color);

  &:not(:disabled):not(.disabled):active {
    color: $white-color;
  }
}

.btn-danger {
  @include btn-styles($danger-color, $danger-states-color);

}

.btn-neutral {
  @include btn-styles($white-color, $white-color);
}

.btn {
  &:disabled,
  &[disabled],
  &.disabled {
    @include opacity(.5);
    pointer-events: none;
  }
}

.btn-simple {
  border: $border;
  border-color: $default-color;
  padding: $padding-btn-vertical - 1 $padding-round-horizontal - 1;
  background-color: $transparent-bg;
}

.btn-simple,
.btn-link {
  &.disabled,
  &:disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background: $transparent-bg;
    }
  }
}

.btn-link {
  border: $none;
  padding: $padding-base-vertical $padding-base-horizontal;
  background: $transparent-bg;
  color: $light-gray;

  &:hover {
    box-shadow: none !important;
    transform: none !important;
  }
}

.btn-lg {
  @include btn-size($padding-large-vertical, $padding-large-horizontal, $font-size-large, $border-radius-large);
}

.btn-sm {
  @include btn-size($padding-small-vertical, $padding-small-horizontal, $font-size-base, $border-radius-small);
}

.btn-wd {
  min-width: 140px;
}

.btn-group.select {
  width: 100%;
}

.btn-group .btn.active {
  box-shadow: 2px 2px 6px rgba(0, 0, 0, .4);
  transform: translateY(-1px);
  -webkit-transform: translateY(-1px);
}

.btn-group.select .btn {
  text-align: left;
}

.btn-group.select .caret {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  right: 8px;
}

.btn-round {
  border-width: $border-thin;
  border-radius: $btn-round-radius;
  // padding-right: $padding-round-horizontal;
  // padding-left: $padding-round-horizontal;


  &.btn-simple {
    padding: $padding-btn-vertical - 1 $padding-round-horizontal - 1;
  }
}

.no-caret {
  &.dropdown-toggle::after {
    display: none;
  }
}
